$(document).ready(function() {

// BurgerMenu
    $('.menu-btn').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    $('.menu2-btn').click(function () {
        $(this).toggleClass('burger--active')
        $('.sidebar__menu').toggleClass('sidebar__menu--active')
        $('body').toggleClass('fixed-body')
    })


//Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list option').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list option").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list option')
    let selectWrap = $('.select__wrap')

    $(document).click(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // RandomImages
    // function Displayrndimages() {
    //
    //     var images = [
    //         'img/info-img2.webp',
    //         'img/info-img3.webp',
    //         'img/info-img4.webp',
    //     ]
    //
    //     var Rndimages = images[Math.floor(Math.random() * images.length)]
    //     var image = "<img src='"+ Rndimages +"'>"
    //
    //     document.getElementById('showrndimages').innerHTML = image
    // }
    //
    // setInterval(Displayrndimages,'1500')

    // Calculator

    
    // Ranges
    let rangeBlocks = document.querySelectorAll("._range-block")

    rangeBlocks.forEach(rangeBlock => {
        let rangeSlider = rangeBlock.querySelector(".range-slider")
        let id = rangeBlock.getAttribute("data-range")
        let rangeNumber = document.querySelector(`.range-number[data-range="${id}"]`);
        let rangeInput = rangeBlock.querySelector(".range-input")
        let rangeInputs = [rangeInput]
        let min = +rangeBlock.getAttribute("data-min")
        let max = +rangeBlock.getAttribute("data-max")
        let pips = [
            +rangeBlock.getAttribute("data-pips"),
            +rangeBlock.getAttribute("data-pips2"),
            +rangeBlock.getAttribute("data-pips3"),
            +rangeBlock.getAttribute("data-pips4"),
            +rangeBlock.getAttribute("data-pips5"),
            +rangeBlock.getAttribute("data-pips6"),
            +rangeBlock.getAttribute("data-pips7")
        ]

        if (rangeSlider) {
            noUiSlider.create(rangeSlider, {
                start: 0,
                connect: [true, false],
                step: 1,
                range: {
                    'min': min,
                    'max': max
                },
                pips: {
                    mode: 'values',
                    values: pips
                }
            });

        }
        if (rangeSlider) {
            rangeSlider.noUiSlider.on('update', function(values, handle) {
                rangeInputs[handle].value = Math.round(values[handle])
                rangeNumber.innerHTML = Math.round(values[0]);
            })
            rangeInputs.forEach((el, index) => {
                el.addEventListener('change', (e) => {
                    setRangeSlider(index, e.currentTarget.value)
                })
            })
        }
        const setRangeSlider = (i, value) => {
            let array = [null, null];
            array[i] = value;

            rangeSlider.noUiSlider.set(array);
        };
    })


})


// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}



